import React from 'react';
import styles from "../Users.module.css";
import {UserType} from "../../../api/api";
import {DescriptionBlock} from "./DescriptionBlock/DescriptionBlock";
import {Avatar} from "./Avatar/Avatar";

export const User = React.memo(({user, followInProgress, setUnFollowUser, setFollowUser,  ...props}:UserPropsType) => {
    const {id, name, status, followed} = user

    return (
        <div className={styles.usersPage}>
            <div className={styles.avatarBlock}>
                <Avatar userId={id}/>
                <button disabled={followInProgress.some((idItem: number) => idItem === id)} className={styles.follow}
                        onClick={() => {followed ? setUnFollowUser(id) : setFollowUser(id)}}>
                    {followed ? 'UnFollow' : 'Follow'}
                </button>
            </div>
            <DescriptionBlock name={name} status={status} />
        </div>
    )
})

// types
type UserPropsType = {
    user: UserType
    followInProgress: Array<number>
    setUnFollowUser: (userId: number) => void
    setFollowUser: (userId: number) => void
}
