import React from 'react';
import styles from "./Pagination.module.css";


export const Pagination = ({totalItemsCount, pageSize, currentPage, startPageNumber, onPageChanged, setStartPageNumber}
                               : PaginationType) => {

    let pagesCount = Math.ceil(totalItemsCount / pageSize)
    let pages = []
    for (let i = 1; i <= pagesCount; i++) {
        pages.push(i)
    }
    const portionPages = 10

    const onClickNextButton = () => {
        setStartPageNumber(startPageNumber + portionPages)
    }
    const onClickPrevButton = () => {
        setStartPageNumber(startPageNumber - portionPages)
    }

    return (
        <div className={styles.pagination}>
            {startPageNumber > 1 && <button onClick={onClickPrevButton}>PREV</button>}
            {
                pages
                    .slice(startPageNumber - 1, startPageNumber + portionPages - 1)
                    .map(p => {
                        return <span key={p}
                                     onClick={() => {onPageChanged(p)}}
                                     className={currentPage === p ? styles.selectedPage : styles.page}>{p}</span>
                    })}
            {startPageNumber + portionPages < pagesCount && <button onClick={onClickNextButton}>NEXT</button>}
        </div>
    )
}

//types
type PaginationType = {
    totalItemsCount: number
    pageSize: number
    currentPage: number
    startPageNumber: number
    setStartPageNumber: (startPageNumber: number) => void
    onPageChanged: (pageNumber: number) => void
}