import React from 'react';
import styles from "../../Users.module.css";

export const DescriptionBlock = React.memo(({name, status, ...props}:DescriptionBlockPropsType) => {
    return (
        <div className={styles.descriptionBlock}>
            <div className={styles.nameUserBlock}>
                <div className={styles.nameUser}>{name}</div>
                <div className={styles.statusUser}>{status}</div>
            </div>
            <div className={styles.locationBlock}>
                <div className={styles.country}> Country</div>
                <div className={styles.city}>City</div>
            </div>
        </div>
    )
})

//types
type DescriptionBlockPropsType = {
    name: string
    status: null | string
}