import React from 'react';
import styles from './Users.module.css'
import {Pagination} from "../common/Pagination/Pagination";
import {User} from "./User/User";
import {UserType} from "../../api/api";
import {setStartPageNumber} from "../../Redux/usersReducer";


export const Users = (props:UsersPropsType) => {
    const {users, totalUsersCount, pageSize, currentPage, setStartPageNumber, startPageNumber, onPageChanged, followInProgress, setUnFollowUser,
        setFollowUser} = props

    return (
        <div className={styles.usersPageContainer}>
            <Pagination totalItemsCount={totalUsersCount} pageSize={pageSize} currentPage={currentPage}
                        startPageNumber={startPageNumber} onPageChanged={onPageChanged} setStartPageNumber={setStartPageNumber}/>

            {
                users.map((u: UserType) => <User key={u.id} user={u} followInProgress={followInProgress}
                                                 setUnFollowUser={setUnFollowUser} setFollowUser={setFollowUser}/>)
            }
        </div>
    )
}

//types
type UsersPropsType = {
    users: Array<UserType>
    totalUsersCount: number
    pageSize: number
    currentPage: number
    startPageNumber: number
    setStartPageNumber: (startPageNumber: number) => void
    onPageChanged: (pageNumber: number) => void
    followInProgress: Array<number>
    setUnFollowUser: (userId: number) => void
    setFollowUser: (userId: number) => void
}